import React, {useContext, useRef} from "react";
import {Plus, Trash2} from "react-feather";
import {Swiper, SwiperProps, SwiperRef, SwiperSlide,} from "swiper/react";
import {Navigation, Pagination} from "swiper/modules";

import {Product} from "../models";

import {CurrentTableIdxContext, CurrentTableIdxContextType} from "../app-context/currentTableIdx-context";
import {TablesContext, TablesContextType} from "../app-context/tables-context";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

import "./Tables.css"

function Tables() {

  const {
    currentTableIdx,
    setCurrentTableIdx
  } = useContext(CurrentTableIdxContext) as CurrentTableIdxContextType;

  const {
    tables,
    addTable,
    updateTableAtIdx,
    removeTableAtIdx
  } = useContext(TablesContext) as TablesContextType;

  const swiperRef = useRef<SwiperRef>(null);

  return (
    <div className="tables">
      <Swiper
        modules={[Navigation, Pagination]}
        slidesPerView={1}
        navigation
        pagination
        ref={swiperRef}
        onSlideChange={(props: SwiperProps) => setCurrentTableIdx(props.realIndex)}
      >
        {tables.map((table, index) => {

          const removeProduct = (product: Product) => {
            let table = tables[currentTableIdx];
            let cart = table.cart;
            let item = cart.items.find(item => item.product.id === product.id);
            if (item !== undefined) {
              if (item.quantity === 1) {
                cart.items = cart.items.filter(item => item.product.id !== product.id)
              } else {
                item.quantity -= 1;
              }
            }
            updateTableAtIdx(currentTableIdx, table);
          };

          return (
            <SwiperSlide key={index ?? -1}>
              <div className="head">Tisch: {table.number ?? "-"}</div>
              <div className="cart">
                <table>
                  <thead>
                  <tr>
                    <td style={{'width': '2.5rem'}}></td>
                    <td>Name</td>
                    <td style={{'width': '5rem'}}>Preis</td>
                  </tr>
                  </thead>
                  <tbody>
                  {table.cart.items.map(item => {
                    return (
                      <tr key={'item_' + item.product.name + item.product.amount}
                          onClick={() => removeProduct(item.product)}>
                        <td className="quantity">{item.quantity}x</td>
                        <td className="name"
                            dangerouslySetInnerHTML={{__html: item.product.name + ' ' + (item.product.amount ?? '')}}></td>
                        <td className="price">{(item.quantity * item.product.price).toLocaleString('de', {
                          style: 'currency',
                          currency: 'EUR',
                        })}
                        </td>
                      </tr>
                    );
                  })}
                  </tbody>
                </table>
              </div>
              <div className="sum price">
                {table.cart.items.reduce((a, b) => a + b.quantity * b.product.price, 0).toLocaleString('de', {
                  style: 'currency',
                  currency: 'EUR',
                })}
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
      <div className="tableNumber">
        {tables[currentTableIdx]?.number}
      </div>
      <div className="actions">
        <button onClick={() => {
          let tableNumber;
          do {
            tableNumber = window.prompt("neue Tischnummer (1-99)");
            if (tableNumber === null) {
              return;
            }
          } while (isNaN(parseInt(tableNumber)) || parseInt(tableNumber) < 1 || parseInt(tableNumber) >= 100)
          addTable({number: parseInt(tableNumber), cart: {items: []}});
          setTimeout(() => {
            swiperRef.current?.swiper.slideTo(tables.length, 0)
          });
        }} className="add"><Plus/></button>
        <button onClick={() => {
          removeTableAtIdx(currentTableIdx);
        }} className="clear"><Trash2/></button>
      </div>
    </div>
  );
}

export default Tables;
