import {Product} from "../models";

export const complete = (product: {
    name: string;
    amount?: string;
    price?: number;
    className: string;
    variants?: undefined;
  }
  |
  {
    name: string;
    variants: { name?: string; amount?: string; price?: number; }[];
    amount?: string;
    price?: number;
    className: string;
  }) => {
  if (product.variants !== undefined) {
    product.variants = product.variants.map((variant) => {
      const {variants: _, ...newProduct} = product;
      return {id: newProduct.name + ' / ' + variant.name + ' / ' + (variant.amount ?? newProduct.amount), ...newProduct, ...variant};
    });
  } else {
    (product as Product).id = product.name + ' / ' + product.amount;
  }
  return product as Product;
}
