import React, {useContext, useRef} from "react";
import {ArrowUpLeft} from "react-feather";
import {Swiper, SwiperRef, SwiperSlide,} from "swiper/react";
import {Pagination} from "swiper/modules";

import {Product} from "../models";
import {areas} from "../config/config_2024-07-26_Kirtag";

import {CurrentTableIdxContext, CurrentTableIdxContextType} from "../app-context/currentTableIdx-context";
import {TablesContext, TablesContextType} from "../app-context/tables-context";

import "swiper/css";
import "swiper/css/pagination";

import "./Products.css"


function Products() {

  const {
    currentTableIdx
  } = useContext(CurrentTableIdxContext) as CurrentTableIdxContextType;

  const {
    tables,
    updateTableAtIdx
  } = useContext(TablesContext) as TablesContextType;

  const [productWithVariants, setProductWithVariants] = React.useState<Product>();

  const swiperRef = useRef<SwiperRef>(null);
  const [lastSwiperIndex, setLastSwiperIndex] = React.useState<number>();

  const renderButtons = (product: Product) => {
    return (
      <button key={"btn_" + product.name + product.amount} className={product.className}
              onClick={(event) => {
                selectProduct(product);
                event.stopPropagation();
              }}>
        <div className="name" dangerouslySetInnerHTML={{__html: product.name}}></div>
        <div className="amount">{product.amount}</div>
      </button>
    );
  };

  const selectProduct = (product: Product) => {
    if (product.variants !== undefined) {
      setLastSwiperIndex(swiperRef.current?.swiper.realIndex)
      setProductWithVariants(product)
      return;
    }

    let table = tables[currentTableIdx];
    let cart = table.cart;
    let item = cart.items.find(item => item.product.id === product.id);
    if (item !== undefined) {
      item.quantity += 1
    } else {
      cart.items.push({
        quantity: 1,
        product: product
      })
    }
    updateTableAtIdx(currentTableIdx, table);

    // reset view after adding product
    // if (productWithVariants !== undefined) {
    //   resetProductWithVariants();
    // }
  };

  const resetProductWithVariants = () => {
    setProductWithVariants(undefined);
    setTimeout(() => {
      swiperRef.current?.swiper.slideTo(lastSwiperIndex, 0)
      setLastSwiperIndex(undefined);
    });
  }

  return (
    <div className="products">
      <Swiper
        modules={[Pagination]}
        slidesPerView={1}
        pagination
        ref={swiperRef}
      >
        {productWithVariants !== undefined ?
          <SwiperSlide onClick={resetProductWithVariants} >
            <div className="head" dangerouslySetInnerHTML={{__html: productWithVariants.name}}></div>
            <div className="Products">
              {productWithVariants.variants?.map(renderButtons)}
            </div>
          </SwiperSlide>
          :
          areas.map(area => {
            return (
              <SwiperSlide key={"area_" + area.name}>
                <div className="head" dangerouslySetInnerHTML={{__html: area.name}}></div>
                <div className="Products">
                  {area.products.map(renderButtons)}
                </div>
              </SwiperSlide>
            );
          })}
      </Swiper>
      <ArrowUpLeft color="lightgray" size="100"
                   style={{
                     "position": "absolute",
                     "marginTop": "-100px",
                     "right": 0,
                     "display": productWithVariants !== undefined ? "inherit" : "none"}}/>
    </div>
  );
}

export default Products;
