import {useState} from "react";

import {CurrentTableIdxContext} from "./app-context/currentTableIdx-context";
import {Table, TablesContext} from "./app-context/tables-context";

import Tables from "./components/Tables";
import Products from "./components/Products";

function App() {

  const [currentTableIdx, setCurrentTableIdx] = useState<number>(0);
  const [tables, setTables] = useState<Table[]>([{ cart: { items: [] }}]);

  const addTable = (table: Table) =>
    setTables([...tables, table])
  const updateTableAtIdx = (index: number, newTable: Table) =>
    setTables(tables.map((table, idx) => idx === index ? newTable : table))
  const removeTableAtIdx = (index: number) => {
    if (index === 0) {
      if (window.confirm('Möchtest du die Bestellung wirklich löschen?')) {
        setTables([{cart: {items: []}}, ...tables.slice(1)])
      }
    } else {
      if (window.confirm('Möchtest du den Tisch wirklich löschen?')) {
        // instead of toSpliced
        const array = tables.slice(); // Create a shallow copy of the array
        array.splice(index, 1); // Apply the splice operation
        setTables(array);
      }
    }
  }

  return (
    <CurrentTableIdxContext.Provider value={{ currentTableIdx, setCurrentTableIdx }}>
      <TablesContext.Provider value={{ tables, addTable, updateTableAtIdx, removeTableAtIdx}}>
        <Tables/>
        <Products/>
      </TablesContext.Provider>
    </CurrentTableIdxContext.Provider>
  );
}

export default App;
