import {createContext} from "react"
import {Product} from "../models";

interface Cart {
  items: {
    quantity: number,
    product: Product
  }[]
}

export interface Table {
  number?: number
  cart: Cart
}

export interface TablesContextType {
  tables: Table[]
  addTable: (table: Table) => void
  updateTableAtIdx: (index: number, table: Table) => void
  removeTableAtIdx: (index: number) => void
}

export const TablesContext = createContext<TablesContextType | null>(null)
